@import (reference) "../../common.less";
.radio-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 12px;
  .radio-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
    transition: all .5s;
    color: @white;
    .radio {
      display: inline-block;
      position: relative;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: solid @white 1px;
      margin-right: 5px;
      transition: all .5s;
    }
  }

  .radio-container:hover {
    color: @yellow;
    .radio {
      border-color: @yellow;
    }
  }

  .radio-container-check {
    color: @yellow;
    .radio {
      border-color: @yellow;
    }
    .radio:after {
      content: "";
      position: absolute;
      left: 3px;
      top: 3px;
      right: 3px;
      bottom: 3px;
      border-radius: 50%;
      background: @yellow;
    }
  }
}
