@import (reference) "../../common.less";
@setting-width: 420px;
@setting-title-height: 60px;

.kline {
  height: 100%;
  display: flex;
  flex-direction: row;
  .kline-setting-container {
    height: 100%;
    width: @setting-width;
    background: #2E3038;
    .kline-setting-title-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      color: @white;
      font-size: 18px;
      line-height: @setting-title-height;
      text-align: center;
      background: #3F4243;
      padding-left: 20px;
      padding-right: 20px;
      .kline-setting-title-icon-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        .kline-setting-title-icon {
          font-size: 26px;
          margin-right: 10px;
          text-decoration: none;
          color: @white;
          transition: all .5s;
          &:hover {
            color: @second-white;
            transform: rotate(360deg);
          }
        }
        .kline-setting-title-version {
          font-size: 16px;
        }
      }

      .kline-setting-button {
        border: solid 1px @second-white;
        font-size: 14px;
        color: @second-white;
      }
    }
    .kline-setting-list-container {
      padding-top: 20px;
      .kline-setting-list-title {
        font-size: 16px;
        border-bottom-right-radius: 10px;
        border-top-right-radius: 10px;
        color: @white;
        padding-left: 16px;
        border-left: solid 4px @yellow;
      }
      .kline-setting-list {
        margin-top: 6px;
        margin-bottom: 20px;
        padding-left: 20px;
        padding-right: 20px;
        font-size: 14px;
        color: @gary;
        .kline-setting-list-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          height: 40px;
          border-bottom: solid 1px #404040;
          transition: all .5s;
          &:hover {
            color: @yellow;
          }
        }
      }
    }
  }

  .kline-chart {
    height: 100%;
    width: calc(100% - @setting-width);
  }
}
