@import (reference) "../../common.less";

.color-picker {
  display: flex;
  flex-direction: row;
  align-items: center;
  .color-picker-value {
    display: inline-block;
    width: 132px;
    line-height: 26px;
    letter-spacing: 1px;
    background: @component-background;
    padding-left: 10px;
    color: @white;
    font-size: 12px;
    border-radius: 4px;
    margin-right: 5px;
  }
  .rc-color-picker-wrap {
    height: 24px;
    .rc-color-picker-trigger {
      border: none;
      height: 24px;
      width: 24px;
    }
  }
}
