@import (reference) "../../common.less";
.home {
  height: 100%;
  background-image: url("../../assets/bg.jpg");
  background-size: cover;
  background-position: center 0;
  .home-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-image: linear-gradient(140deg, rgba(61, 66, 96, 0.6) 20%, rgba(21, 23, 34, 0.8));
    color: @second-gary;
    font-size: 32px;

    .home-avatar {
      width: 5em;
      border-radius: 50%;
      border: 0.12em solid @white;
      transition: all .5s;
    }

    .home-avatar:hover {
      border-color: @yellow;
    }

    .home-name {
      letter-spacing: 0.12px;
      font-size: 1.6em;
      color: @white;
      margin-bottom: 0.3em;
    }

    .home-introduce {
      margin: 0;
      letter-spacing: 0.12em;
      font-size: 0.8em;
    }

    .home-contact {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 5.5em;
      padding-bottom: 3em;
      padding-top: 1.5em;
      .home-icon {
        font-size: 0.9em;
        transition: all .5s;
        text-decoration: none;
        color: @second-gary;
        .home-icon-qr-container {
          display: none;
          position: relative;
          flex-direction: column;
          justify-content: center;
          .home-icon-qr-triangle {
            position: absolute;
            left: 0.37em;
            top: 0;
            width:0;
            height:0;
            border-left: 0.16em solid transparent;
            border-right: 0.16em solid transparent;
            border-bottom: 0.2em solid @white;
          }
          .home-icon-qr {
            position: absolute;
            border-radius: 4px;
            top: 0.15em;
            width: 4em;
            left: -1.55em;
          }
        }
      }
      .home-icon:hover {
        color: @white;
        .home-icon-qr-container {
          display: flex;
        }
      }
    }

    .home-footer {
      position: absolute;
      text-align: center;
      bottom: 0;
      letter-spacing: 0.12em;
      font-size: 0.55em;
      padding-bottom: 1em;
      a {
        color: @white;
        text-decoration: none;
      }
    }
  }

  @media screen and (max-width: 1024px) {
    .home-content {
      font-size: 24px;
    }
  }

  @media screen and (max-width: 768px) {
    .home-content {
      font-size: 18px;
    }
  }
}

.home:after{
  content: "";
  width:100%;
  height:100%;
  position: absolute;
  left:0;
  top:0;
  background: inherit;
  filter: blur(5px);
  z-index: -1;
}
