@import (reference) "../../common.less";
.select {
  position: relative;
  outline: none;
  width: 160px;
  font-size: 12px;
  color: @white;
  .select-value-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 26px;
    padding-left: 10px;
    padding-right: 8px;
    border: solid 1px @white;
    background: @component-background;
    border-radius: 4px;
    transition: all .5s;
    cursor: pointer;
    .select-value-arrow {
      font-style: normal;
      display: inline-block;
      width: 0;
      height: 0;
      transition: all .5s;
      border-top: 5px solid @second-white;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
    }
  }

  .select-drop-list {
    display: none;
    border-radius: 4px;
    background: @component-background;
    box-shadow: 0 0 0 2px rgba(32, 32, 32, .2);
    position: absolute;
    width: 160px;
    left: 0;
    top: 26px;
    z-index: 10;
    border: solid 1px #404040;
    .select-drop-list-row {
      color: white;
      line-height: 28px;
      padding-left: 10px;
      cursor: pointer;
      transition: all .5s;
      &:hover {
        color: @yellow;
      }
    }
  }

}

.select:hover {
  color: @yellow;
  .select-value-container {
    border-color: @yellow;
    box-shadow: 0 0 0 2px rgba(255, 152, 0, .2);
    .select-value-arrow {
      border-top-color: @yellow;
    }
  }
}

.select-show {
  .select-value-container {
    .select-value-arrow {
      transform-origin: center center;
      transform: rotate(180deg);
    }
  }
  .select-drop-list {
    display: block;
  }
}
