@import (reference) "../../common.less";
.rc-input-number {
  background: @component-background;
  width: 160px;
  .rc-input-number-input {
    background: transparent;
    color: @white;
    text-align: left;
    padding-left: 10px;
  }

  .rc-input-number-handler-up-inner, .rc-input-number-handler-down-inner {
    color: @second-white;
  }
  &:hover {
    border-color: @yellow;
    .rc-input-number-handler-up, .rc-input-number-handler-wrap {
      border-color: @yellow;
    }
  }
}

.rc-input-number-focused {
  border-color: @yellow;
  box-shadow: 0 0 0 2px rgba(255, 152, 0, .2);
}
